import React, {useState} from 'react';
import axios from 'axios';
import {useDispatch , useSelector} from 'react-redux';
import {detailsUser , updateUserProfile} from '../actions/userActions';
import LoadingBox from '../components/LoadingBox';
import MessageBox from '../components/MessageBox';
import { USER_UPDATE_PROFILE_RESET } from '../constants/userConstants';
export default function ProfileScreen(props) {
    const [name,setName] = useState('');
    const [email,setEmail] = useState('');
    const [password,setPassword] = useState('');
    const [confirmPassword,setConfirmPassword] = useState('');
    const [sellerName, setSellerName] = useState('');
    const [sellerLogo, setSellerLogo] = useState('');
    const [sellerDescription, setSellerDescription] = useState('');
    const userSignin  = useSelector(state => state.userSignin);
    const {userInfo} = userSignin;
    const userDetails = useSelector(state => state.userDetails);
    const {loading,error,user} = userDetails;
    console.log(user);
    const userUpdateProfile = useSelector(state=>state.userUpdateProfile);
    const {
        success:successUpdate,
        error:errorUpdate,
        loading:loadingUpdate
    } = userUpdateProfile;
    const dispatch = useDispatch();
    React.useEffect(()=>{
        if(!user){
            dispatch({type:USER_UPDATE_PROFILE_RESET});
            dispatch(detailsUser(userInfo._id));
        }else{
            setName(user.name);
            setEmail(user.email);
            if(user.seller){
                setSellerName(user.seller.name);
                setSellerLogo(user.seller.logo);
                setSellerDescription(user.seller.description);
            }
        }
    },[dispatch,userInfo._id,user])
    const [loadingUpload , setLoadingUpload] = useState(false);
    const [errorUpload , setErrorUpload] = useState('');
    const uploadFileHandler = async (e)=>{
     
        const file = e.target.files[0];
        const bodyFormData = new FormData();
        bodyFormData.append('image',file);
        setLoadingUpload(true);
        try {
            const {data} = await axios.post('/api/uploads/s3',bodyFormData,{
                headers:{
                    'Content-Type':'multipart/form-data',
                    Authorization:`BEARER ${userInfo.token}`
                }
            })
            setSellerLogo(data);
            setLoadingUpload(false);
        } catch (error) {
            setErrorUpload(error.message);
            setErrorUpload(false);
        }
    }
    const submitHandler = (e) =>{
        e.preventDefault();
        if(password!==confirmPassword){
            alert('Password and confirm password are not matched');
        }else{
            dispatch(updateUserProfile({userId:user._id,
                name,
                email,
                password,
                sellerName,
                sellerLogo,
                sellerDescription}));
        }
    }
    return (
        <div>
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>User Profile</h1>
                </div>
                {loading ? (
                    <LoadingBox/>
                ): error ? (
                    <MessageBox variant="danger" children={error}/>
                ):(
                    <>
                    {loadingUpdate && (<LoadingBox/>)}
                    {errorUpdate && (<MessageBox variant="danger" children={errorUpdate}/>)}
                    {successUpdate && (<MessageBox variant="success" children="Profile Updated successfully"/>)}
                        <div>
                            <label htmlFor="name">Name</label>
                            <input
                                type="text"
                                id="name"
                                placeholder="Enter Name"
                                value={name}
                                onChange={(e)=>setName(e.target.value)}
                            />
                        </div>
                        <div>
                            <label htmlFor="email">Email</label>
                            <input
                                type="email"
                                id="email"
                                placeholder="Enter email"
                                value={email}
                                onChange={(e)=>setEmail(e.target.value)}
                            />
                        </div>
                        <div>
                            <label htmlFor="password">Password</label>
                            <input
                                type="password"
                                id="password"
                                placeholder="Enter Password"
                                onChange={(e)=>setPassword(e.target.value)}
                            />
                        </div>
                        <div>
                            <label htmlFor="confirmPassword">Confirm Password</label>
                            <input
                                type="password"
                                id="confirmPassword"
                                placeholder="Enter Confirm Password"
                                onChange={(e)=>setConfirmPassword(e.target.value)}
                            />
                        </div>
                        {userInfo.isSeller && (
                            <>
                                <h2>Seller</h2>
                                <div>
                                    <label htmlFor="sellerName">Seller Name</label>
                                    <input
                                        type="text"
                                        id="sellerName"
                                        placeholder="Enter Seller Name"
                                        value={sellerName}
                                        onChange={(e)=>setSellerName(e.target.value)}
                                    />
                                </div>
                                <div>
                                    <label htmlFor="sellerLogo">Seller Logo</label>
                                    <input 
                                        type="file"
                                        id="sellerLogo"
                                        label="Choose Your Logo"
                                        onChange={uploadFileHandler}
                                    />
                                    {loadingUpload && <LoadingBox/>}
                                    {errorUpload && <MessageBox variant="danger" children={errorUpload} />}
                                </div>
                                <div>
                                    <label htmlFor="sellerDescription">Seller Description</label>
                                    <input
                                        type="text"
                                        id="sellerDescription"
                                        placeholder="Enter Seller Description"
                                        value={sellerDescription}
                                        onChange={(e)=>setSellerDescription(e.target.value)}
                                    />
                                </div>
                            </>
                        )}
                        <div>
                            <label/>
                            <button type="submit" className="primary">
                                Update
                            </button>
                        </div>
                    </>
                )}
            </form>
        </div>
    )
}
